<template>
  <van-overlay
    class="DialogUploader f-center"
    :show="modelValue"
    :lock-scroll="true"
    z-index="10"
    @click="onClose"
  >
    <Column class="w-638 h-1060 p-30 bg-white position-r radius-12" @click.stop>
      <Row class="w-100b label-lg m-b-50" x="c">{{ $t('activity.uploadScreenshot') }}</Row>
      <UploadBox class="w-100b h-322 m-b-35" @onUploadEd="(data) => onUploadEd('twitterImgUrl', data)">
        <img v-if="form.twitterImgUrl" class="w-100b h-322 o-hidden radius-12" :src="form.twitterImgUrl">
        <Column v-else class="w-100b h-322 o-hidden radius-12">
          <Column class="w-100b bg-d f-1 position-r" x="c" y="c">
            <img class="w-98 h-98 " src="@/image/sendBTC/uploader-add.webp">
            <Row class="label-sm position-a bottom-30">{{ $t('activity.uploaderBoxTitle1') }}</Row>
          </Column>
          <Row class="w-100b h-50 bg-info label-sm" x="c" y="c">{{ $t('form.reUpload') }}</Row>
        </Column>
      </UploadBox>
      <UploadBox class="w-100b h-322 m-b-60" @onUploadEd="(data) => onUploadEd('telegramImgUrl', data)">
        <img v-if="form.telegramImgUrl" class="w-100b h-322 o-hidden radius-12" :src="form.telegramImgUrl">
        <Column v-else class="w-100b h-322 o-hidden radius-12">
          <Column class="w-100b bg-d f-1 position-r" x="c" y="c">
            <img class="w-98 h-98 " src="@/image/sendBTC/uploader-add.webp">
            <Row class="label-sm position-a bottom-30">{{ $t('activity.uploaderBoxTitle2') }}</Row>
          </Column>
          <Row class="w-100b h-50 bg-info label-sm" x="c" y="c">{{ $t('form.reUpload') }}</Row>
        </Column>
      </UploadBox>

      <van-button class="w-100b h-86 m-b-20 radius-42 m-m-16 label-lg" round type="primary" @click="submit">{{ $t('activity.confirmUpload') }}</van-button>
      <Row class="w-100b label-sm" x="c">{{ $t('activity.uploadTips') }}</Row>

      <Row class="w-100b position-a -bottom-100 " x="c">
        <div class="border border-c-white radius-50b p-5" :style="{ 'border-width': '2px' }" @click="onClose">
          <van-icon class="c-white" size="20" name="cross" />
        </div>
      </Row>
    </Column>
  </van-overlay>
</template>

<script setup>
const emit = defineEmits(['submit', 'update:modelValue'])

const props = defineProps({
  modelValue: {
    default: false,
  },
  id: {},
})

const form = reactive({
  twitterImgUrl: '',
  telegramImgUrl: '',
})

const onUploadEd = (k, data) => {
  console.log('k, data', k, data)
  form[k] = data.url
}

const submit = async () => {
  const params = {
    activityId: props.id,
    ...form,
  }
  await activityApi.registerSendBTCSubmit(params)
  onClose()
}
const onClose = () => {
  form.twitterImgUrl = ''
  form.telegramImgUrl = ''
  emit('update:modelValue', false)
  emit('submited')
}

onMounted(() => {
})

onBeforeMount(async () => {
})
</script>

<style lang="scss">
.DialogUploader{
}
</style>
