<template>
  <van-overlay
    class="DialogReceive f-center "
    :show="modelValue"
    :lock-scroll="true"
    z-index="10"
    @click="onClose"
  >
    <Column class="w-534 h-534 bg-white position-r" @click.stop>
      <img v-if="type == typeMap.receipt" class="w-100b f-1" src="@/image/sendBTC/DialogReceive-bg.png">
      <img v-else-if="type == typeMap.finish" class="w-100b f-1" src="@/image/sendBTC/DialogFinish-bg.png">
      <Row class="w-100b h-120" y="c" x="c">
        <div v-if="type == typeMap.receipt" class="btn f-center label-lg bold" @click="onReceipt">{{ $t('form.receipt') }}</div>
        <div v-else-if="type == typeMap.finish" class="btn f-center label-lg bold" @click="onFinish">{{ $t('form.finish') }}</div>
      </Row>
      <Row v-if="type == typeMap.finish" class="w-100b position-a top-330 title-lg" x="c">{{ awardCoinAmount }}</Row>
      <Row class="w-100b position-a -bottom-100 " x="c">
        <div class="border border-c-white radius-50b p-5" :style="{ 'border-width': '2px' }" @click="onClose">
          <van-icon class="c-white" size="20" name="cross" />
        </div>
      </Row>
    </Column>
  </van-overlay>
</template>

<script setup>
const emit = defineEmits(['submit', 'update:modelValue'])

const props = defineProps({
  modelValue: {
    default: false,
  },
  id: {},
})

const typeMap = {
  finish: 'finish',
  receipt: 'receipt',
}
let type = $ref(typeMap.receipt)
let awardCoinAmount = $ref()

const onClose = () => {
  emit('update:modelValue', false)
}

const onReceipt = async () => {
  const data = await activityApi.registerSendBTCReceive({ activityId: props.id })
  awardCoinAmount = data.awardCoinAmount
  type = typeMap.finish
  emit('receipted')
}

const onFinish = () => {
  emit('update:modelValue', false)
}

onMounted(() => {
})

onBeforeMount(async () => {
})
</script>

<style lang="scss">
.DialogReceive{
  .btn{
    width: 422px;
    height: 67px;
    background: #FCD296;
    border-radius: 33px;
  }
}
</style>
