<template>
  <div v-loading="loading" class="UploadBtn pointer">
    <input
      v-if="showInput"
      class="file-input pointer"
      type="file"
      :limit="1"
      :accept="accept"
      @change="inputChange"
    >
    <slot />
  </div>
</template>

<script setup>

const emit = defineEmits()

const props = defineProps({
  accept: {
    default: 'image/jpg, image/jpeg, image/png, image/webp, application/pdf',
  },
})
let showInput = $ref(true)
let loading = $ref(false)
const fileToServer = async (file) => {
  try {
    showInput = false
    loading = true
    const data = await fileApi.uploadFile({ file: file })

    return data[0]
  } finally {
    showInput = true
    loading = false
  }
}

const uploadFile = async (file) => {
  try {
    if (file.size / 1024 / 1024 > 5.2 * 100) {
      showNotify({ type: 'danger', message: $t('public.uploadLimit') })
      return
    }
    const data = await fileToServer(file)

    emit('onUploadEd', data)
  } catch (error) {}
}
const inputChange = async (e) => {
  const files = e.target.files
  for (let i = 0; i < files.length; i++) {
    const file = files[i]
    await uploadFile(file)
  }
}

onBeforeMount(() => {})
</script>

<style lang="scss">
.UploadBtn {
  position: relative;
  display: flex;
  z-index: 9;
  .file-input {
    position: absolute;
    font-size: 0;
    opacity: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
  }
}
</style>
