<script setup>
import { DialogReceive, DialogUploader } from '@/view/activity/components'

const userStore = useUserStore()
const languageStore = useLanguageStore()

let showDialogActivityEnd = $ref(false)
let AcTips = $ref('')
let showDialogUploader = $ref(false)
let showDialogReceive = $ref(false)
const loading = $ref(true)
let startTime = $ref()
let endTime = $ref()
let activityId = $ref()
let isReceiveMoney = $ref()
let kycTask = $ref(0)
let transQuotaTask = $ref(0)
let uploadImgTask = $ref(0)
let transQuota = $ref(0)
let userTransQuota = $ref(0)
let transCoinName = $ref()
let telegramLink = $ref()
let twitterLink = $ref()

const biFinanceUrl = $computed(() => {
  const map = {
    [LANGCN]: 'https://t.me/BiFinance_Globalcn',
    [LANGEN]: 'https://t.me/BiFinance_Global',
  }
  return map[languageStore.locale]
})

const toLink = (link) => {
  window.location.href = link
}

const completeness = $computed(() => {
  let n = [kycTask == 1, transQuotaTask == 1].reduce((n, b) => b ? n + 1 : n, 0)
  if (uploadImgTask == 2) {
    n += 2
  }
  return n
})

const enBtnClass = $computed(() => {
  return languageStore.locale == LANGEN ? '' : ''
})

console.log('platform', platform)

const getData = async () => {
  try {
    const d = await activityApi.registerSendBTCInfo()
    startTime = d.startTime
    endTime = d.endTime
    activityId = d.id
    transCoinName = d.transCoinName
    transQuota = d.transQuota
    telegramLink = d.telegramLink
    twitterLink = d.twitterLink
    return true
  } catch (error) {
    showDialogActivityEnd = error.code == 18040
    AcTips = $t('activity.noActivityTips')
    return false
  }
}

const getUserData = async () => {
  try {
    const ud = await activityApi.registerSendBTCUserInfo()
    kycTask = ud.kycTask
    transQuotaTask = ud.transQuotaTask
    uploadImgTask = ud.uploadImgTask
    isReceiveMoney = ud.isReceiveMoney
    userTransQuota = ud.transQuota
    showDialogReceive = isReceiveMoney == 1
  } catch (error) {
    showDialogActivityEnd = error.code == 18111
    AcTips = $t('activity.acTips1')
    console.log('error', error)
  }
}

const toPage = (path) => {
  if (isEmpty(userStore.token)) {
    winMes.postParent('toRegister')
  } else {
    winMes.postParent(path)
  }
}
const toShare = () => {
  winMes.postParent('toShare')
}
const onUpload = () => {
  if (isEmpty(userStore.token)) {
    winMes.postParent('toRegister')
  } else {
    showDialogUploader = true
  }
}

onBeforeMount(async () => {
  const res = await getData()
  if (res)getUserData()
})
</script>

